.projects-page {
  min-height: 100vh;
  padding-bottom: 4rem;
}

.filter-bar {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin: 2rem 0;
  padding: 0 2rem;
  flex-wrap: wrap;
}

.filter-button {
  background: transparent;
  border: 2px solid var(--primary-color);
  color: var(--primary-color);
  padding: 0.5rem 1.5rem;
  border-radius: 25px;
  cursor: pointer;
  transition: var(--transition);
  font-weight: 600;
  text-transform: capitalize;
}

.filter-button:hover,
.filter-button.active {
  background: var(--primary-color);
  color: var(--background-dark);
  transform: translateY(-2px);
}

.projects-grid {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
}

.project-card {
  background: var(--background-light);
  border-radius: var(--border-radius);
  border: 1px solid rgba(255, 255, 255, 0.1);
  overflow: hidden;
  transition: var(--transition);
}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-light);
}

.project-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.project-content {
  padding: 2rem;
}

.project-title {
  color: var(--primary-color);
  margin-bottom: 1rem;
  font-size: 1.3rem;
}

.project-description {
  margin-bottom: 1.5rem;
  line-height: 1.6;
  color: var(--text-secondary);
}

.project-technologies {
  margin-bottom: 1rem;
}

.tech-tag {
  display: inline-block;
  background: rgba(0, 212, 255, 0.1);
  color: var(--primary-color);
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 0.9rem;
  margin: 0.2rem;
  font-weight: 500;
}

.project-links {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}

.project-link {
  color: var(--primary-color);
  text-decoration: none;
  font-weight: 600;
  padding: 0.5rem 1rem;
  border: 1px solid var(--primary-color);
  border-radius: 20px;
  transition: var(--transition);
  text-align: center;
  flex: 1;
}

.project-link:hover {
  background: var(--primary-color);
  color: var(--background-dark);
  transform: translateY(-2px);
}

.status-badge {
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 600;
  margin-bottom: 1rem;
  display: inline-block;
}

.status-badge.completed {
  background: rgba(34, 197, 94, 0.1);
  color: #22c55e;
}

.status-badge.in-progress {
  background: rgba(251, 191, 36, 0.1);
  color: #fbbf24;
}

.no-projects {
  text-align: center;
  padding: 4rem 2rem;
  color: var(--text-secondary);
  font-size: 1.1rem;
}

.project-header {
  margin-bottom: 1rem;
}

.project-name {
  background: rgba(124, 58, 237, 0.1);
  color: var(--accent-color);
  padding: 0.2rem 0.6rem;
  border-radius: 12px;
  font-size: 0.8rem;
  font-weight: 600;
  margin-left: 0.5rem;
}

.project-actions {
  margin-top: 1.5rem;
}

.details-button {
  background: transparent;
  border: 2px solid var(--primary-color);
  color: var(--primary-color);
  padding: 0.7rem 1.5rem;
  border-radius: 25px;
  cursor: pointer;
  transition: var(--transition);
  font-weight: 600;
  width: 100%;
}

.details-button:hover {
  background: var(--primary-color);
  color: var(--background-dark);
  transform: translateY(-2px);
}

.project-details {
  margin-top: 1.5rem;
  padding-top: 1.5rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  animation: fadeIn 0.3s ease-in-out;
}

.detailed-description h4,
.media-gallery h4 {
  color: var(--primary-color);
  margin-bottom: 1rem;
  font-size: 1.1rem;
}

.detailed-description p {
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.media-gallery {
  margin-top: 1.5rem;
}

.media-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
}

.media-item {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  overflow: hidden;
  transition: var(--transition);
}

.media-item:hover {
  transform: translateY(-3px);
  box-shadow: var(--shadow-light);
}

.media-item img {
  width: 100%;
  height: 100px;
  object-fit: cover;
}

.pdf-preview {
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: rgba(0, 212, 255, 0.1);
  gap: 0.5rem;
}

.pdf-icon {
  font-size: 1.5rem;
}

.pdf-preview a {
  color: var(--primary-color);
  text-decoration: none;
  font-size: 0.8rem;
  font-weight: 600;
}

.pdf-preview a:hover {
  text-decoration: underline;
}

.media-caption {
  padding: 0.5rem;
  font-size: 0.8rem;
  color: var(--text-secondary);
  text-align: center;
  line-height: 1.3;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 768px) {
  .filter-bar {
    gap: 0.5rem;
  }
  
  .filter-button {
    padding: 0.4rem 1rem;
    font-size: 0.9rem;
  }
  
  .projects-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .project-links {
    flex-direction: column;
  }
  
  .media-grid {
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    gap: 0.8rem;
  }
  
  .media-item img,
  .pdf-preview {
    height: 80px;
  }
}
